<template>
  <div id="app" class="bg-info">
    <div class="container-fluid">
         <p class="bg-primary title">{{title}}</p>
         <p class="glyphicon glyphicon-home"><span>{{home}}</span></p>
         <main-body></main-body>
    </div>
  </div>
</template>

<script>
import main from './components/main'

export default {
  name: 'App',
  data(){
    return{
      title:"Deri",
      home:"首页"
    }
  },
  components: {
    "main-body":main
  }
}
</script>

<style scoped>
*{
  margin: 0;
  padding: 0;
}
.title{
  height: 50px;
  line-height: 50px;
  font-size: 25px;
}
.glyphicon-home{
  font-size: 15px;
  padding: 10px 0;
}
.glyphicon-home span{
  display: inline-block;
  text-indent:5px;
}
#app{

}
</style>



