<template>
  <div id="app-main">
    <div class="main-body">
      <p class="form-inline">
        <label>姓名:</label>
        <input type="text" name="" value="" class="form-control form-inline" placeholder="请输入姓名" v-model="search" >
        <button class="btn btn-primary glyphicon glyphicon-search">查询</button>
      </p>
      <table class="table table-striped table-bordered">
        <thead>
          <tr>
            <th v-for="item in items" class="text-center">
              {{item}}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in filterusers">
            <td class="text-center">
              {{user.id}}
            </td>
            <td class="text-center">
              {{user.personname}}
            </td>
            <td class="text-center">
              {{user.activeplace}}
            </td>
             <td class="text-center">
              {{user.activetime}}
            </td>
          </tr>
        </tbody>
      </table>
      <div>
        <div class="pull-left">
            <span>第</span>
            <span>{{pageCurrent}}</span>
            <span>页&nbsp;每页</span>
            <select v-model="pageSize" v-on:change="selectpageSize">
              <option value="3" selected>3</option>
              <option value="4">4</option>
            </select>
            <span>条</span>
        </div>
        <div class="pull-right">
            <nav aria-label="Page navigation">
              <ul class="pagination">
                  <li></li>
                  <li v-on:click="firstPage"><span>首页</span></li>
                  <li v-on:click="pre"><span>前页</span></li>
                  <li v-on:click="next"><span>后页</span></li>
                  <li v-on:click="endPage"><span>末页</span></li>
                  <li><span>共{{pageAll_a}}页</span></li>
                  <li><span>第<input type="text" style="width:40px;" v-model="pageCurrent"/>页</span><button class="btn btn-primary" v-on:click="pageTo">跳转</button></li>
                </ul>
             </nav>
             
          </div>
          <div style="clear:both;">
          </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "app-main",
  data () {
    return {
      items:["序号","姓名","住址","登记时间"],
      users:[],
      search:"",
      pageSize:3,
      pageCurrent:1,
      pageto:1,
      usersLength:11,
      pageAll:3,
      n:1,
      start:0,
      end:3,
      pageAll_a:4 
  }
},
  created(){
    this.$http.get('http://deri.org.cn/testjson/').then((data)=>{
      console.log(data.body.list);
      this.users=data.body.list
    })
  },
  methods:{
    selectpageSize:function(){
      this.start=0;
      this.end=this.pageSize;
      this.pageAll=Math.floor(this.usersLength/this.pageSize);
      if(this.usersLength%this.pageSize>0){
        this.pageAll_a=this.pageAll+1;
      }
    },
    next:function(){
      this.usersLength=this.users.length;
      this.n++;
      if(this.n>this.pageAll+1){
        this.n=this.pageAll;
        this.start=this.pageSize*(this.n);
        this.end=this.usersLength;
      }else{
      this.pageCurrent=this.n;
      this.start=this.pageSize*(this.n-1);
      this.end=this.pageSize*(this.n)
      }
    },
    pre:function(){
      this.n--;
      if(this.n<1){
        this.n=1;
        this.pageCurrent=this.n;
        this.start=0;
        this.end=this.pageSize;
      }else{
        this.pageCurrent=this.n;
        this.start=this.pageSize*(this.n-1);
        this.end=this.pageSize*(this.n)
      }
    },
    firstPage:function(){
      this.n=1;
      this.pageCurrent=1;
      this.start=0;
      this.end=this.pageSize;
    },
    endPage:function(){
      if(this.usersLength%this.pageSize>0){
        this.pageCurrent=Math.floor(this.usersLength/this.pageSize+1);
      }
      this.n=this.pageCurrent;
      this.start=this.pageSize*(this.pageAll);
      this.end=this.usersLength;
    },
    pageTo:function(){
      this.n=this.pageCurrent;
      this.start=this.pageSize*(this.n-1);
      this.end=this.pageSize*(this.n)
    }
  },
  computed:{
    filterusers:function(){
      return this.users.slice(this.start,this.end).filter((user)=>{
        return user.personname.match(this.search)
      })
    }
  }
}
</script>
<style scoped>
.main-body{
  padding:20px 0;
  background: #FFF;

}
.pull-left{
  line-height: 80px;
}
</style>



